<template>
    <div>
        <in-body :mbx="['首页','其他组件','table表格']">

          <a href="https://www.iviewui.com/components/table" target="_blank">
            <Button type="primary">点击查看说明文档</Button>
          </a>

            <Divider orientation="left">基础用法</Divider>
            <p>表格的最简单用法。</p>
            <table-s1></table-s1>

            <Divider orientation="left">斑马纹</Divider>
            <p>设置属性 <code>stripe</code> ，表格会间隔显示不同颜色，用于区分不同行数据。</p>
            <table-s2></table-s2>

            <Divider orientation="left">特定样式</Divider>
            <p><b>行：</b>通过属性 <code>row-class-name</code> 可以给某一行指定一个样式名称。</p>
            <p><b>列：</b>通过给列 <code>columns</code> 设置字段 <code>className</code> 可以给某一列指定一个样式。</p>
            <p><b>单元格：</b>通过给数据 <code>data</code> 设置字段 <code>cellClassName</code> 可以给任意一个单元格指定样式。</p>
            <table-s3></table-s3>

            <Divider orientation="left">固定表头</Divider>
            <table-s4></table-s4>

          <Divider orientation="left">固定列</Divider>
          <table-s5></table-s5>


        </in-body>
    </div>
</template>

<script>
    import inBody from '../inBody'
    import tableS1 from '../b/tableS1'
    import tableS2 from '../b/tableS2'
    import tableS3 from '../b/tableS3'
    import tableS4 from '../b/tableS4'
    import tableS5 from '../b/tableS5'
    export default {
        name: "myTable"
        ,components:{
            inBody
            ,tableS1
            ,tableS2
            ,tableS3
            ,tableS4
        ,tableS5
        }
    }
</script>

<style scoped>
p { margin-bottom: 10px;}
code {
    display: inline-block;
    background: #f7f7f7;
    font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
    margin: 0 3px;
    padding: 1px 5px;
    border-radius: 3px;
    color: #666;
    border: 1px solid #eee;
}
</style>



